import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router/routes'
import Jwt from "vue-jwt-decode"
import axios from 'axios'
import Notify from "vue-notifyjs"
Vue.use(Notify);


Vue.prototype.notify = Notify;
Vue.prototype.axios = axios;
Vue.prototype.jwt = Jwt;
Vue.prototype.indexurl = "http://gabyslaw-001-site1.itempurl.com/";
Vue.prototype.createform = {
  data: function (input)
  {
    var formdata = new FormData();
    for (var i in input)
    {
      formdata.append(i, input[i]);
    }
    return formdata;
  }
};
// Router

Vue.prototype.token = function ()
{
  return getToken();
};

var indexurl = "http://gabyslaw-001-site1.itempurl.com/"
Vue.prototype.setDatas = () =>
{
  setCourses();
  setInstitutions();
  setUsers();
  setRoles();
}


function getToken()
{
  if (!!window.localStorage.getItem("token"))
  {
    var token = JSON.parse(window.localStorage.getItem("token"));
    var expiration = new Date(token.exp).getTime();
    var now = new Date().getTime();

    if (expiration > now)
    {
      return token.token;
    } else
    {
      window.localStorage.removeItem("token");
      return false;
    }
  } else
  {
    return false;
  }
}

function setCourses()
{
  if (!!window.localStorage.getItem("token"))
  {
    axios
      .get(indexurl + "api/Course/all", {
        headers: {
          Authorization: "Bearer " + getToken()
        }
      })
      .then(response =>
      {
        if (response.status == "200")
        {
          store.commit("setCourses", response.data);
        }
      });
  }

}

