var data = {
    state: {
        users: [],
        institutions: [],
        courses: [],
        roles: []
    },
    mutations: {
        setUsers(state, users)
        {
            state.users = users;
        },
        setInstitution(state, inst)
        {
            state.institutions = inst;
        },
        setCourses(state, courses)
        {
            state.courses = courses;
        }, setRoles(state, roles)
        {
            state.roles = roles;
        }
    }
}

export default data;